<template>
  <div>
    <v-header>
      <div slot="left"></div>
      <div slot="right" class="h-right">
        <a v-link="{name:'home'}"><span class="toggle img"></span></a></div>
    </v-header>

    <v-content>
      <v-banner :banner="'server'"></v-banner>

      <div class="section">
        <div class="matter" v-link="{name:'server-calendar'}">
          <span class="img calendar"></span>
          <div class="title">我的日程</div>
          <span class="img transfer" style="top: .15rem;"></span>
        </div>
      </div>

      <div class="section" style="margin-bottom:0;">
        <div class="matter">
          <span class="img task"></span>
          <div class="title">最新任务</div>
        </div>
      </div>

      <div class="task" v-if="taskDay.length > 0">
        <div class="line-box">
          <ul class="work-line">
            <li v-for="n in 2">
              <ul class="dis-flex week-ul">
                <li class="flex-1" v-for="weekday in weekdays">
                  {{weekday | week-format}}
                </li>
              </ul>
              <ul class="dis-flex date-ul">
                <li class="flex-1" v-for="item in showDay" v-if="n*7 <= $index && $index < (n+1)*7"
                    :class="[item.value == currDay ? 'curr':'',taskDay.indexOf(item.value) > -1 ? 'use-day':'']"
                    @click="getTaskByDay(item.value)">
                  {{item.date}}
                </li>
              </ul>
            </li>
          </ul>
        </div>
        <ul class="page-ul">
          <li v-for="n in 2" :class="$index == pageIndex ? 'curr':''"></li>
        </ul>
        <ul class="task-ul">
          <li v-for="item in tasks" class="task-li">
            <ul class="box">
              <li class="service-detail">
                <p class="name ellipsis" style="max-width:75%;margin-top:0;">
                  {{item.name}}</p>
                <span class="status">{{item.statusName}}</span>
                <p class="work-time mg-t10 clear-fix"><span class="img clock"></span>{{item.workTime}}</p>
                <p class="address clear-fix" style="margin: .05rem 0;"><span class="img location"></span>{{item.address}}
                </p>
              </li>
              <li class="options">
                <span class="partner">点匠人:{{item.buyerName}}</span>
                <div class="btns right">
                  <input type="button" value="取消" class="cancel button-style" v-if="item.status==0"
                         @click="cancel(item.id,$index,item.status,'取消')"/>

                  <input type="button" value="提前完结" class="cancel button-style" v-if="item.status==1"
                         @click="cancel(item.id,$index,item.status,'提前完结')"/>

                  <input type="button" value="撤回" class="cancel button-style"
                         v-if="item.operater == user_id && (item.status==2||item.status==3)"
                         @click="commitBack(item.id,$index,item.status)"/>

                  <span class="show-remark"
                        v-if="item.remark"
                        @click="showRemark(item.remark,item.status)">
                                查看原因
                                </span>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
      <div :class="['no-task','none_bg',isLogin ? '' : 'need-login']" v-else>
        <span class="img bg04"></span>
        <p class="remark" v-if="isLogin">您还没有最新任务</p>
        <p class="remark" v-else>
          亲，您还没有<a class="color-link" v-link="{name:'login',query:{from:'/home/server'}}">登录</a>哟~
        </p>
      </div>
    </v-content>

    <v-footer>
    </v-footer>
  </div>
</template>
<script>
  import VHeader from '../components/Header'
  import VContent from '../components/Content'
  import VFooter from '../components/Footer'
  import VBanner from '../components/Banner'
  import $ from 'jquery'
  import {zeroFill} from '../filter'
  import {url, lt} from '../const'
  import {toast} from '../assets/js/toast'
  import {slider} from '../assets/js/slider'

  export default {
    route: {
      data(){
        if (this.isReady) {
          this.$broadcast('reload-footer');
          return {
            state: !this.state
          }
        }
      }
    },
    ready(){
      this.$on('ready', function () {
        this.$broadcast('login-check');
      });
      this.$emit('ready');
      this.isReady = true;
    },
    data () {
      return {
        isReady: false,
        state: false,

        tasks: [],
        isLogin: false,
        showDay: [],
        currDay: 0,
        taskDay: [],
        weekdays: ['7', '1', '2', '3', '4', '5', '6'],
        pageIndex: 0,
        user_id: 0,
        operateObj: 2
      }
    },
    methods: {
      showRemark(msg, status){
        toast.message(msg, status == 2 ? '提前完结原因' : '取消原因');
      },
      cancel(id, index, status, msg){
        this.optTask = {id: id, index: index, status: status};
        toast.commit(this.doCommit, `请说明${msg}原因`, `请填写${msg}原因`);
      },
      commitBack(id, index, status){
        let vm = this;
        let msg = (status == 2 ? '提前完结' : '取消');
        toast.conform(`确定撤回${msg}操作吗？`, function () {
          vm.doBack(id, index);
        });
      },
      doCommit(content){
        if (!content) {
          toast.alert('内容不能为空');
          return;
        }
        let _url = this.optTask.status == 0 ? url.taskCancel : url.taskFinish;
        this.$http.get(_url,
                {
                  id: this.optTask.id,
                  remark: content,
                  operateObj: this.operateObj
                }).then(
                function ({data:{success, data, resultMsg}}) {
                  toast.doCommit();
                  if (success) {
                    toast.alert("操作成功");
                    this.tasks[this.optTask.index].status = data.status;
                    this.tasks[this.optTask.index].remark = data.remark;
                    this.tasks[this.optTask.index].operater = data.operater;
                    this.tasks[this.optTask.index].statusName = data.statusName;
                  } else {
                    toast.alert(resultMsg);
                  }
                }
        );
      },
      doBack(id, index){
        this.$http.get(url.taskBack,
                {
                  id: id,
                  operateObj: this.operateObj
                }).then(
                function ({data:{success, data, resultMsg}}) {
                  if (success) {
                    toast.alert("撤回成功");
                    this.tasks[index].status = data.status;
                    this.tasks[index].remark = data.remark;
                    this.tasks[index].operater = data.operater;
                    this.tasks[index].statusName = data.statusName;
                  } else {
                    toast.alert(resultMsg);
                  }
                }
        );
      },
      getUseDay(){
        this.$http.get(url.newestDay).then(function ({data:{success, data, resultMsg}}) {
                  if (success) {
                    this.taskDay = data;
                    this.getTaskByDay(this.taskDay[0]);
                    this.$nextTick(function () {
                      this.loadBox();
                    });
                  } else {
                    toast.alert(resultMsg);
                  }
                }
        );
      },
      loadBox(){
        let vm = this;
        let w = $(window).width();
        console.log(`w:${w}`);
        $('.work-line').css('width', (w * 2) + 'px');
        $('.work-line li').css('width', w + 'px');
        slider.ready('.work-line', {
          loop: false,//循环切换
          auto: false,//自动切换
          scroll_time: 500,//滚动时长
          callback: function (index) {
            vm.pageIndex = index;
          }
        });
      },
      getTaskByDay(value){
        if (this.taskDay.indexOf(value) < 0) {
          return;
        }
        let d = value + '';
        if (d.length < 8) {
          return;
        }
        this.currDay = value;
        let day = d.substring(0, 4) + '-' + d.substring(4, 6) + '-' + d.substring(6, 8);
        this.$http.get(url.newestTask, {day: day}).then(function ({data:{success, data, resultMsg}}) {
                  if (success) {
                    this.tasks = data;
                  } else {
                    toast.alert(resultMsg);
                  }
                }
        );
      }
    },
    events: {
      'is-login': function (isLogin) {
        if (isLogin) {
          this.showDay = [];
          let time = new Date();
          let day = time.getDay();
          let sunday = new Date(time.getTime() - 24 * 60 * 60 * day * 1000);
          this.showDay.push(
                  {
                    value: parseInt('' + sunday.getFullYear() + zeroFill(sunday.getMonth() + 1) + zeroFill(sunday.getDate())),
                    date: sunday.getDate()
                  });
          let date;
          for (let i = 1; i < 14; i++) {
            date = new Date(sunday.getTime() + 24 * 60 * 60 * i * 1000);
            this.showDay.push({
              value: parseInt('' + date.getFullYear() + zeroFill(date.getMonth() + 1) + zeroFill(date.getDate())),
              date: date.getDate()
            });
          }
          this.getUseDay();
          this.user_id = lt.getJson(lt.keys.user).userId;
        }
        this.isLogin = isLogin;
      }
    },
    watch: {
      'state': function () {
        this.taskDay = [];
        this.currDay = {};
        this.$emit('ready');
      }
    },
    components: {
      VHeader,
      VFooter,
      VContent,
      VBanner
    }
  }
</script>
<style scoped>
  .img.toggle {
    background-image: url(../assets/img/icon/i_change.png);
  }

  .matter .img {
    top: .12rem;
    position: absolute;
  }

  .matter .title {
    text-indent: .25rem;
    width: auto;
  }

  .line-box {
    width: 100%;
    overflow: hidden;
  }

  .line-box .work-line {
    clear: both;
  }

  .work-line ul {
    width: 100%;
  }

  .work-line li {
    float: left;
    overflow: hidden;
    width: 100%;
  }

  .work-line .week-ul {
    height: .25rem;
    line-height: .25rem;
    background-color: #e0e0e0;
    font-size: .12rem;
    text-align: center;
  }

  .work-line .date-ul {
    height: .6rem;
    line-height: .6rem;
    text-align: center;
    color: #333;
    background-color: #fff;
  }

  .date-ul li {
    border-right: .01rem solid #f5f5f5;
    position: relative;
  }

  .date-ul .curr {
    color: #fff;
    background-color: #00CDA9;
  }

  .date-ul .use-day:after {
    content: '';
    position: absolute;
    width: .05rem;
    height: .05rem;
    border-radius: 50%;
    background-color: #e0e0e0;
    bottom: 10%;
    left: 50%;
    margin-left: -.025rem;
  }

  .date-ul .use-day.curr:after {
    background-color: #fff;
  }

  .page-ul {
    height: .1rem;
    width: .5rem;
    margin: 0 auto;
  }

  .page-ul li {
    margin-top: .02rem;
    width: .05rem;
    height: .05rem;
    margin-left: .1rem;
    border-radius: 50%;
    float: left;
    background-color: #fff;
  }

  .page-ul li.curr {
    background-color: #ffb600;
  }

  .task-ul .task-li {
    background-color: #fff;
    margin-bottom: .1rem;
  }

  .task-li .box {
    position: relative;
  }

  .service-detail {
    padding: .1rem;
    border-bottom: .01rem solid #e5e5e5;
  }

  .service-detail .name {
    font-size: .16rem;
    color: #333;
  }

  .service-detail .status {
    position: absolute;
    right: .1rem;
    top: .1rem;
    color: #ffb600;
  }

  .service-detail .work-time, .service-detail .address {
    position: relative;
    text-indent: .16rem;
    font-size: .12rem;
  }

  .options {
    height: .5rem;
    line-height: .5rem;
  }

  .options .partner {
    position: absolute;
    height: .4rem;
    color: #999;
    font-size: .12rem;
    left: .1rem;
  }

  .img.clock, .img.location {
    position: absolute;
    left: 0;
    top: .01rem;
    width: .14rem;
    height: .14rem;
    background-size: .14rem;
  }

  .img.clock {
    background-image: url(../assets/img/icon/clock.png);
  }

  .img.location {
    background-image: url(../assets/img/icon/location-2.png);
  }

  .options input {
    margin-right: .1rem;
  }

  .options .show-remark {
    margin-right: .1rem;
    float: left;
    font-size: .12rem;
    color: #999;
  }

  .btns .cancel {
    height: .3rem;
    padding: 0 .1rem;
    border: .01rem solid #e6e6e6;
    background-color: #fff;
  }

  .img.calendar {
    top: .1rem;
    width: .22rem;
    height: .22rem;
    background-size: .22rem;
    background-image: url(../assets/img/icon/calendar.png);
  }

  .img.task {
    width: .18rem;
    height: .18rem;
    background-size: .18rem;
    background-image: url(../assets/img/icon/toolBox02.png);
  }

  .no-task {
    width: 100%;
    height: 2.25rem;
  }

  .img.bg04 {
    background-image: url(../assets/img/bg/null_icon04.png);
  }
</style>



